<template>
  <div class="login-container">
    <el-form autoComplete="on" :model="loginForm" :rules="loginRules" ref="loginForm" label-position="left" label-width="0px"
      class="card-box login-form">
      <h3 class="title">登录</h3>
      <el-form-item prop="userName">
        <el-input name="userName" type="text" v-model="loginForm.userName" autoComplete="on" placeholder="用户名" />
      </el-form-item>
      <el-form-item prop="passWord">
        <el-input name="passWord" type="passWord" v-model="loginForm.passWord" autoComplete="on"
          placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleLogin">
          登录
        </el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="text" style="width:100%;" :loading="loading" @click.native.prevent="handleRegister">
          注册
        </el-button>
      </el-form-item>
<!-- 
      <div class='tips'>
        <span style="margin-right:20px;">用户名: admin</span>
        </span> 密码: admin</span>
      </div> -->
    </el-form>
  </div>
</template>

<script>
import { isvalidUserName } from '@/utils/validate'

export default {
  name: 'login',
  created(){
    this.initLocalStorage();
  },
  data() {
    const validateUsername = (rule, value, callback) => {
      if (!isvalidUserName(value)) {
        callback(new Error('用户名只可以是字母数字下划线'))
      } else {
        callback()
      }
    }
    const validatePass = (rule, value, callback) => {
      if (value.length < 5 || value.length>20) {
        callback(new Error('密码不能小于5位,也不能大于20'))
      } else {
        callback()
      }
    }
    return {
      loginForm: {
        userName: '',
        passWord: ''
      },
      loginRules: {
        userName: [{ required: true, validator: validateUsername }],
        passWord: [{ required: true, validator: validatePass }]
      },
      loading: false
    }
  },
  methods: {
    // 记录登录账号
    initLocalStorage(){
      if(window.localStorage.apiHouseLoginForm){
        try{
          this.loginForm = Object.assign(this.loginForm,JSON.parse(window.localStorage.apiHouseLoginForm))
        }catch(e){
        }
      }
    },
    handleLogin() {
      window.localStorage.apiHouseLoginForm = JSON.stringify(this.loginForm);
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true
          this.$store.dispatch('Login', this.loginForm).then(() => {
            this.$store.dispatch('GetUserInfo').then(res=>{
              this.$router.push({ path: '/' })
            }).catch(err=>{
              this.$message.error('获取用户信息失败')
            }).finally(()=>{
              this.loading = false
            })
          }).finally(() => {
            this.loading = false
          })
        } else {
          // 输入校验有误
          return false
        }
      })
    },
    handleRegister(){
      this.$router.push({ path: '/register' })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss">
  $bg:#2d3a4b;
  $dark_gray:#889aa4;
  $light_gray:#eee;

  .login-container {
    height: 100vh;
    background-color: $bg;
    input:-webkit-autofill {
      -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
      -webkit-text-fill-color: #fff !important;
    }
    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: $light_gray;
      height: 47px;
    }
    .el-input {
      display: inline-block;
      height: 47px;
      width: 85%;
    }
    .tips {
      font-size: 14px;
      color: #fff;
      margin-bottom: 10px;
    }
    .svg-container {
      padding: 6px 5px 6px 15px;
      color: $dark_gray;
      vertical-align: middle;
      width: 30px;
      display: inline-block;
      &_login {
        font-size: 20px;
      }
    }
    .title {
      font-size: 26px;
      font-weight: 400;
      color: $light_gray;
      margin: 0px auto 40px auto;
      text-align: center;
      font-weight: bold;
    }
    .login-form {
      position: absolute;
      left: 0;
      right: 0;
      width: 400px;
      padding: 35px 35px 15px 35px;
      margin: 120px auto;
    }
    .el-form-item {
      border: 1px solid rgba(255, 255, 255, 0.1);
      background: rgba(0, 0, 0, 0.1);
      border-radius: 5px;
      color: #454545;
    }
    .show-pwd {
      position: absolute;
      right: 10px;
      top: 7px;
      font-size: 16px;
      color: $dark_gray;
      cursor: pointer;
    }
    .thirdparty-button{
      position: absolute;
      right: 35px;
      bottom: 28px;
    }
  }
</style>



