<template>
  <el-row>
    <!-- 左侧域名操作 -->
    <el-col :span="12">
      <!-- 当前生效域名 -->
      <div style="height:70px;line-height:70px;">
        <span>当前生效域名：</span>
        <el-button type='text'>{{userInfo.currentHost}}</el-button>
        <!-- <el-button size="mini" type="warning" @click="next">下一步</el-button> -->
      </div>
      <!-- 一键设置 -->
      <div style="margin-bottom:40px">
        <span>一键设置所有接口来源</span>
        <el-button size="mini" @click="clickAllApiSetOrigin(true)">本地</el-button>
        <el-button size="mini" @click="clickAllApiSetOrigin(false)">线上</el-button>
      </div>
      <!-- 域名罗列 -->
      <p>
        <span>域名列表</span>
      <!-- <el-button size="mini" type="success">增加</el-button> -->
      <el-button size="mini" @click="clickEditHost">{{!this.isEditHost?'编辑域名':'结束编辑'}}</el-button>
      <el-button size="mini" @click="goNextPage()">下一步</el-button>
      </p>
      <el-radio-group v-model="userInfo.currentHost" @change="onChangeHost">
        <div :key="index" v-for="(url,index) in userInfo.hostList" style="padding:10px">
          <el-radio :label="url" >{{url}}</el-radio>
          <el-button v-if="isEditHost" @click="clickRemoveHost(url)" size="mini" style="margin-left:20px" type="danger">删除</el-button>
        </div>
      </el-radio-group>
      
      <div style="height:30px"></div>
      <!-- 添加域名 -->
      <el-input size="mini" v-model="inputHost" placeholder="要添加的域名，要带协议，格式为：http://api.com" style="width:350px"></el-input>
      <el-button size="mini" type="warning" @click="clickAddHost">添加域名</el-button>
      <div style="height:10px"></div>
    </el-col>
    <!-- 最近浏览的接口 -->
    <el-col :span="12">
      <!-- 当前生效域名 -->
      <div style="height:30px;margin-top:20px;">
        <span>最近浏览的接口：</span>
      </div>
      <div v-for="(item,index) in historyApiList" :key="index" style="">
        <el-button @click="clickApiUrl(item.url)" type="text" style="text-align:left;line-height:20px;padding:0;margin-top:15px">{{item.url}} <br>{{item.desc}}</el-button>
      </div>
    </el-col>
  </el-row>
</template>
<script>
  import { mapState, mapActions } from "vuex";
  import {setCurrentHost,sendAddHost,getApiData,sendRemoveHost,updateMany_apiDataByHost} from '../../api/control'
  export default {
    data() {
      return {
        apiDataList:[],
        loading:false,
        isEditHost:false,
        hostList:[],
        activeNames:[1],
        inputHost:'',
        currentHost:''
      }
    },
    created(){
    },
    computed:{
      ...mapState(['userInfo','historyApiList'])
    },
    components:{

    },
    methods: {
      ...mapActions(['GetUserInfo']),
      // 批量设置本地or线上
      async clickAllApiSetOrigin(isUseLocalData){
        await updateMany_apiDataByHost({isUseLocalData})
        if(isUseLocalData){
          this.$message.success('接口数据源全部设为“本地数据”')
        }else{
          this.$message.success('接口数据源全部设为“线上数据”')
        }
      },
      clickApiUrl(url){
        this.$router.push({name:'Backups',query:{currentApiUrl:url}})
      },
      clickEditHost(){
        this.isEditHost = !this.isEditHost
      },
      async clickRemoveHost(host){
        await sendRemoveHost(host)
        await this.GetUserInfo()
      },
      // 切换域名
      async onChangeHost(host){
        await setCurrentHost(host)
        await this.GetUserInfo()
        this.goNextPage()
      },
      goNextPage(){
        if(!this.userInfo.currentHost){
          this.$message.error('当前无生效域名，请添加域名')
          return
        }
        let {currentHost} = this
        this.$router.push({name: 'ApiData', params: { currentHost }})
      },
      async clickAddHost(){
        let resList = await sendAddHost(this.inputHost)
        await this.GetUserInfo()
      }
    }
  }
</script>
<style scoped lang="scss">
    .markup-top{

    }
</style>


