<template>
  <div class="login-container">
    <el-form autoComplete="on" :model="registerForm" :rules="loginRules" ref="registerForm" label-position="left" label-width="0px"
      class="card-box login-form">
      <h3 class="title">注册</h3>
      <el-form-item prop="userName">
        <el-input name="userName" type="text" v-model="registerForm.userName" autoComplete="on" placeholder="新用户名"/>
      </el-form-item>
      <el-form-item prop="passWord">
        <el-input name="passWord" type="text" v-model="registerForm.passWord" autoComplete="on"
          placeholder="新密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="registerSubmit">
          注册
        </el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="text" style="width:100%;" :loading="loading" @click.native.prevent="goLoginPage">
          返回登录
        </el-button>
      </el-form-item>

      <!-- <div class='tips'>
        <span style="margin-right:20px;">用户名: admin</span>
        </span> 密码: admin</span>
      </div> -->
    </el-form>
  </div>
</template>

<script>
import { isvalidUserName,isvalidPassWord } from '@/utils/validate'
import { register } from '@/api'
export default {
  name: 'login',
  data() {
    const validateUserName = (rule, value, callback) => {
      if (!isvalidUserName(value)) {
        callback(new Error('只能包含字母、数字、下划线_'))
      } else {
        callback()
      }
    }
    const validatePass = (rule, value, callback) => {
      if (!isvalidPassWord(value)) {
        callback(new Error('只能包含字母、数字、下划线_'))
      }else if(value.length<5 ||value.length>20){
        callback(new Error('密码至少要5位，但不可高于20位'))
      } else {
        callback()
      }
    }
    return {
      registerForm: {
        userName: '',
        passWord: ''
      },
      loginRules: {
        userName: [{ required: true, validator: validateUserName }],
        passWord: [{ required: true, validator: validatePass }]
      },
      loading: false
    }
  },
  methods: {
    validateForm (){
      return new Promise((resolve,reject)=>{
        this.$refs.registerForm.validate(valid => {
          valid && resolve() || reject()
        })
      })  
    },
    async registerSubmit(){
      await this.validateForm()
      await register(this.registerForm)
      await this.$store.dispatch('Login', this.registerForm)
      this.$store.dispatch('GetUserInfo').then(res=>{
        this.$router.push({ path: '/' })
      }).catch(err=>{
      }).finally(()=>{
        this.loading = false
      })
    },
    goLoginPage() {
      this.$router.push({ path: '/' })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss">
  $bg:#2d3a4b;
  $dark_gray:#889aa4;
  $light_gray:#eee;

  .login-container {
    height: 100vh;
    background-color: $bg;
    input:-webkit-autofill {
      -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
      -webkit-text-fill-color: #fff !important;
    }
    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: $light_gray;
      height: 47px;
    }
    .el-input {
      display: inline-block;
      height: 47px;
      width: 85%;
    }
    .tips {
      font-size: 14px;
      color: #fff;
      margin-bottom: 10px;
    }
    .svg-container {
      padding: 6px 5px 6px 15px;
      color: $dark_gray;
      vertical-align: middle;
      width: 30px;
      display: inline-block;
      &_login {
        font-size: 20px;
      }
    }
    .title {
      font-size: 26px;
      font-weight: 400;
      color: $light_gray;
      margin: 0px auto 40px auto;
      text-align: center;
      font-weight: bold;
    }
    .login-form {
      position: absolute;
      left: 0;
      right: 0;
      width: 400px;
      padding: 35px 35px 15px 35px;
      margin: 120px auto;
    }
    .el-form-item {
      border: 1px solid rgba(255, 255, 255, 0.1);
      background: rgba(0, 0, 0, 0.1);
      border-radius: 5px;
      color: #454545;
    }
    .show-pwd {
      position: absolute;
      right: 10px;
      top: 7px;
      font-size: 16px;
      color: $dark_gray;
      cursor: pointer;
    }
    .thirdparty-button{
      position: absolute;
      right: 35px;
      bottom: 28px;
    }
  }
</style>



