<template>
    <div class="markup-content" v-cloak v-loading="loading">
      <el-row :gutter="20">
        <el-col :span='10'>
          <div style="font-size:14px">
            <div>当前域名</div>
            <div>{{userInfo.currentHost}}</div>
          </div>
          <div style="margin-top:30px">
            <p>接口列表 <br>
              <el-button size="mini" @click="clickUpdateApiList">刷新</el-button>
              <el-button size="mini" @click="clickAddApi">新增</el-button>
              <el-button size="mini" @click="clickAllApiSetOrigin(true)">一键本地</el-button>
              <el-button size="mini" @click="clickAllApiSetOrigin(false)">一键线上</el-button>
              <el-button size="mini" @click="clickAllSetBackups(false)">一键备份</el-button>
            </p>
            <el-collapse v-model="currentApiUrl" @change="onChangeApi" accordion>
              <el-collapse-item 
                v-for="item in apiList" 
                :key="item._id" 
                :title="item.path" 
                :name="item.url"
              >
                <p style="display:flex">
                  <span style="color:#00BF6C;with:82px;">接口说明：</span>
                  <span style="flex:1">
                    <span v-if="!isEditingDesc && item.desc" @click="isEditingDesc = true">{{item.desc}}</span>
                    <el-input v-else v-model="item.desc" placeholder="可以在这里添加接口描述" @blur="onBlurInputApiDesc(item)" size="mini"></el-input>
                  </span>
                  <!-- <el-button size="mini" >修改</el-button> -->
                </p>
                <!-- collapse-item conent beigin -->
                <span style="color:#00BF6C;">数据来源：</span>
                <el-radio-group v-model="item.isUseLocalData" @change="onChange_isUseLocalData(item)">
                  <el-radio :label="true">本地</el-radio>
                  <el-radio :label="false">线上</el-radio>
                </el-radio-group>
                <p>
                  <el-button @click="clickEditApi(item)" type="warning" size="mini" >详情</el-button>
                  <el-button @click="clickChangeApi(item)" size="mini" >修改</el-button>
                  <el-button @click="updateApi(item)" size="mini" >刷新</el-button>
                  <el-button @click="clickRemoveApi(item)" type="danger" size="mini" style="float:right;margin-right:10px">删除</el-button>
                </p>
                <p style="color:#00BF6C">更新时间：{{item.time}}</p>
                <!-- collapse-item conent end -->
              </el-collapse-item>
            </el-collapse>
          </div>    
        </el-col>
        <el-col :span='14' style="border:1px solid #eee;height:100%;background:#eee;border-radius:10px">
          <p>
            <el-button v-if="isUseLocalData && isChangeApiCont" @click="saveEdit" type="success" size="mini" >保存为缓存</el-button>
            <span v-if="isUseLocalData===false" style="color:#888;font-size:14px;">实时线上数据，无需保存</span>
          </p>
          <el-input  
            v-model="apiContStr" 
            @click="clickApiCont"
            type="textarea" 
            :rows="27" 
            placeholder="接口返回值">
          </el-input>
        </el-col>
      </el-row>
      <!-- dialog -->
      <el-dialog title="新增接口" :visible.sync="dialog.visible" width="80%">
         <el-form ref="dialogForm" :model="dialog.form" status-icon label-width="90px">
          <el-form-item label="路径path :" prop="path" :rules="{required:true,message:'接口路径不能为空'}">
            <el-input 
              v-model="dialog.form.path" 
              placeholder="输入接口路径，如：/publick/userInfo"
            ></el-input>
          </el-form-item>
          <el-form-item label="接口描述 :">
            <el-input 
              v-model="dialog.form.desc" 
              placeholder="输入接口描述，如：获取用户信息接口"
            ></el-input>
          </el-form-item>
          <el-form-item label="接口内容 :" prop="apiCont" :rules="{required:true,message:'接口内容不能为空'}">
            <el-input 
              v-model="dialog.form.apiCont" 
              type="textarea" 
              rows="10" 
              placeholder="输入接口内容，如：{ret:1,data:[]}"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialog.visible = false">取 消</el-button>
          <el-button type="primary" @click="clickSubmitAddApi">确 定</el-button>
        </div>
      </el-dialog>
      <!-- dialog 修改api 路径path 和 url -->
      <el-dialog title="修改接口路径" :visible.sync="dialogApi.visible" width="70%">
         <el-form ref="dialogApiForm" :model="dialogApi.form" status-icon label-width="90px">
          <el-form-item label="path :" prop="newPath" :rules="{required:true,message:'接口路径不能为空'}">
            <el-input 
              v-model="dialogApi.form.newPath" 
              placeholder="输入新接口路径，如：/publick/userInfo"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogApi.visible = false">取 消</el-button>
          <el-button type="primary" @click="submitApiChangeRes">确 定</el-button>
        </div>
      </el-dialog>
    </div>
</template>
<script>
  import {getApiData,getCurrentHost,updateMany_apiDataByHost,change_isUseLocalData,updateOne_apiData_byId,removeOne_api,addOne_api,setUseAllBackups,sendAddAllNewBackups,getApiInfoByPath} from '../../api/control'
  import { format } from "../../utils";
  import { mapState, mapActions } from "vuex";
  export default {
    data() {
      return {
        isEditingDesc: false,
        dialog:{
          visible:false,
          form:{
            path:'',
            desc:'',
            apiCont:''
          }
        },
        dialogApi:{
          visible:false,
          form:{}
        },
        apiInfo:{},//当前api数据体
        apiList:[],//接口列表
        apiContStr:'',//单个接口数据主体
        currentHost:'',//当前域名
        loading:false,
        currentApiUrl:'',
        isUseLocalData:undefined,
        isChangeApiCont:true
      }
    },
    beforeRouteEnter (to, from, next) {
      next(vm => {
        vm.currentApiUrl = vm.$store.state.activeApiUrl
        vm.onChangeApi(vm.currentApiUrl)
        vm.getApiByUrl(vm.currentApiUrl)
      })
    },
    created(){
      if(!this.userInfo.currentHost){
        this.$message.error('当前域名为空，无法使用服务')
        return
      }
      this.initData()
    },
    mounted(){
      
    },
    computed: {
      ...mapState(['historyApiList','activeApiUrl','userInfo']),
    },
    methods: {
      ...mapActions(['setStore_historyApiList','setStore_activeApiUrl']),
      async clickUpdateApiList(){
        await this.getApiList()
        this.$message.success('刷新成功')
        this.currentApiUrl = ''
        this.apiContStr = ''
      },
      async clickAllApiSetOrigin(isUseLocalData){
        await updateMany_apiDataByHost({isUseLocalData})
        if(isUseLocalData){
          setUseAllBackups()
          this.$message.success('设置成功：所有接口都替换为最新-静态备份数据')
        }else{
          this.$message.success('设置成功：数据来源全部切为“线上数据”')
        }
        this.currentApiUrl = ''
        this.apiContStr = ''
        await this.getApiList()
      },
      // 一键批量备份
      async clickAllSetBackups(){
        sendAddAllNewBackups()
        this.$message.success('批量备份成功：已将当前所有接口的缓存，作了最新备份')
      },
      async onBlurInputApiDesc ({_id,desc}){
        this.isEditingDesc = false
        await updateOne_apiData_byId(_id,{desc})
        this.$message.success('接口描述更新成功！')
      },
      clickAddApi(){
        this.dialog.visible = true
        if(this.$refs.dialogForm){
          this.$refs.dialogForm.clearValidate()
        }
      },
      clickSubmitAddApi(){
        this.$refs.dialogForm.validate(valid=>{
          if (valid) {
            this.sendAddApi()
          } else {
            return false;
          }
        })
      },
      // 增加新的接口
      async sendAddApi(){
        let {path,apiCont,desc} = this.dialog.form||{};
        try{
          let apiJson = await format(apiCont)
          let jsonData = apiJson.replace(/[\n\s]/g,'')
          var newData = JSON.parse(jsonData)
          await addOne_api(path,newData,desc)
          this.apiList =  await getApiData({host:this.currentHost})
          this.dialog = {
            visible:false,
            form:{
              path:'',
              apiCont:''
            }
          }
        }catch(e){
          this.$message.error(e.message)
          return
        }
      },
      async initData(){
        this.getApiList();
      },
      // confirm组件 promise化
      _confirm(str){
        return new Promise((resolve,reject)=>{
          this.$confirm(str, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            resolve()
          }).catch(() => {
            reject()          
          });
        })
      },
      // 保存修改后的接口
      async saveEdit(){
        let jsonData = this.apiContStr.replace(/[\n\s]/g,'')
        try{
          var newData = JSON.parse(jsonData)
        }catch(e){
          this.$message.error('保存失败：必须要输入标准Json格式')
          return
        }
        let {_id} = this.apiInfo
        let resObj = await updateOne_apiData_byId(_id,{data:newData})
        this.$message.success('保存成功')
      },
      clickApiCont(){
        this.isChangeApiCont = true
      },
      // 点击切换某一个接口详情
      onChangeApi(url){
        let obj = this.apiList.filter(v=>v.url==url)[0]
        if(!obj){
          return
        }
        this.setStore_historyApiList({url:obj.path,desc:obj.desc||''})
        // this.isChangeApiCont = false
        this.setStore_activeApiUrl(url)
        this.getApiByUrl(url)
      },
      // 点击“编辑&备份”按钮
      clickEditApi(item){
        // this.historyDialog.visible = true
        let {path} = item
        this.$router.push({name:'Backups',query:{currentApiUrl:path}})
      },
      // 修改接口path url路径
      clickChangeApi(item){
        this.dialogApi = {
          visible:true,
          form:{
            ...item,
            newPath:item.path
          }
        }
      },
      // 提交api的 path 路径修改结果
      async submitApiChangeRes(){
        let {form} = this.dialogApi || {}
        if(form.newPath == form.path){
          this.$message.error('path没有变化')
          return
        }
        let resApiList = await getApiData({path:form.newPath})
        if(resApiList && resApiList.length){
          this.$message.error('该路径已存在')
          return
        }
        await updateOne_apiData_byId(form._id,{
          path:form.newPath,
          url:form.host + form.newPath
        })
        this.$message.success("修改成功")
        this.dialogApi.visible = false
        this.getApiList()

      },
      // 刷新接口详情
      async updateApi(item){
        await this.getApiByUrl(item.url)
        this.$message.success('单个接口更新成功')
      },
      // 点击“删除”按钮
      async clickRemoveApi(item){
        await this._confirm('删除接口会一并删除该接口的‘所有备份’，确定？')
        await removeOne_api(item.url)
        this.$message.success('删除成功')
        this.getApiList()
      },
      // 更改当前接口的数据来源 本地 or 线上
      async onChange_isUseLocalData({_id,isUseLocalData}){
        await updateOne_apiData_byId(_id,{isUseLocalData})
        this.isUseLocalData = isUseLocalData
        this.$message.success('更新成功')
        this.getApiList()
      },
      
      // 获取当前生效域名
      async initCurrentHost(){
        let resObj = await getCurrentHost();
        this.currentHost = (resObj||{}).host
      },
      // 获取 api 列表
      async getApiList(){
        this.apiList = await getApiData()
      },
      // 获取单个接口详情
      async getApiByUrl(url){
        let arr = await getApiData({url});
        let resObj = arr[0]||{};
        try{
          this.apiContStr = await format(JSON.stringify(resObj.data));
        }catch(e){
          console.error('apiContStr format Error',e)
        }
        this.apiInfo = resObj
        this.isUseLocalData = resObj.isUseLocalData
      },
    }
  }
</script>
<style rel="stylesheet/css" lang="css" scoped>
  .el-collapse-item__header{
    color:red !important;
  }
</style>


