<template>
    <div v-loading="loading">
      <p>
        <el-button @click="goBack">返回</el-button>
        <el-autocomplete v-model="currentApiPath" @select="clickSearch" :fetch-suggestions="querySearch" :trigger-on-focus="false" style='width:650px' placeholder="请输入要查询的接口"></el-autocomplete>
        <el-button @click="clickSearch" type="success">查询</el-button>
        <span style="margin-left:30px;">数据来源：</span>
        <el-radio-group
          v-model="isUseLocalData" 
          @change="onChange_isUseLocalData"
        >
          <el-radio :label="true">本地</el-radio>
          <el-radio :label="false">线上</el-radio>
        </el-radio-group>
      </p>
      <p style="color:#ff9800;display:flex">
        【接口说明】：
        <span style="flex:1">
          <span v-if="!isEditingDesc && apiInfo.desc" @click="isEditingDesc = true">{{apiInfo.desc}}</span>
          <el-input v-else v-model="apiInfo.desc" placeholder="可以在这里添加接口描述" @blur="onBlurInputApiDesc" size="mini"></el-input>
        </span>
      </p>
      <el-row :gutter='20'>
        <el-col :span="12">
          <el-button type="text">当前API数据</el-button>
          <el-button size="mini" type="success" v-if="isUseLocalData" @click="saveEdit">保存为缓存</el-button>
          <el-button size='mini' type='warning' @click='addNewBackups'>备份</el-button>
          <el-input 
            v-model="apiContStr_current" 
            type="textarea"
            :rows="23" 
          ></el-input>
          <br>
        </el-col>
        <el-col :span="12">
          <el-button type="text">历史备份</el-button>
          <div v-for="(item,index) in backupsList" :key='index' style="margin-bottom:20px">
            <el-input v-model="item.backupName" style="margin-bottom:3px">
              <template slot="prepend">备份名：</template>
            </el-input>
            <el-input 
              v-model="item._temp_apiContStr" 
              
              type="textarea" 
              :rows="6" 
            ></el-input>
            <div style="height:1px"></div>
            <el-button size="mini" type="warning" @click="clickUpdateOneBackups(index,item)">保存备份</el-button>
            <el-button size="mini" type="success" @click="clickSetCurrent(index)">立即启用</el-button>
            <el-button style="float:right" size="mini" type="danger" @click="clickRemoveBackups(index)">删除备份</el-button>
          </div>
          
        </el-col>
      </el-row>
    </div>
</template>
<script>
  import {getApiInfoByPath,getApiUrlList,sendAddNewBackups,sendUpdateOneBackups,sendRemoveBackups,setUseOneBackups,updateOne_apiData_byId} from '../../api/control'
  import { format } from "../../utils";
  export default {
    data() {
      return {
        isEditingDesc:false,
        loading:false,
        currentApiPath:'',//待查询的接口路径
        apiInfo:{},//当前api数据详情
        apiContStr_current:'',//当前api数据 JSON格式
        backupsList:[],
        isUseLocalData:undefined
      }
    },
    watch:{
      
    },
    created(){
      this.currentApiPath = this.$route.query.currentApiUrl||''
      this.getApiData()
    },
    computed: {
    },
    components:{

    },
    methods: {
      async onBlurInputApiDesc (){
        this.isEditingDesc = false
        let {_id,desc} = this.apiInfo
        await updateOne_apiData_byId(_id,{desc})
        this.$message.success('接口描述更新成功！')
      },
      async querySearch(queryUrl, cb) {
        // var restaurants = this.restaurants;
        // var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
        let resList = await getApiUrlList(queryUrl)
        cb(resList.map(v=>Object.create({value:v.path})))
      },
      // 保存修改后的接口
      async saveEdit(){        
        try{
          let res = await format(this.apiContStr_current)
          let jsonData = res.replace(/[\n\s]/g,'')
          var newData = JSON.parse(jsonData)
        }catch(e){
          this.$message.error('保存失败：数据格式有误')
          return
        }
        let {_id} = this.apiInfo||{}
        let resObj = await updateOne_apiData_byId(_id,{data:newData})
        this.$message.success('保存成功')
      },
      async onChange_isUseLocalData(newBoolean){
        let {_id} = this.apiInfo||{}
        // 更改当前接口的数据来源 本地 or 线上
        await updateOne_apiData_byId(_id,{isUseLocalData:newBoolean})
        this.$message.success('切换成功')
      },
      // confirm组件 promise化
      _confirm(str){
        return new Promise((resolve,reject)=>{
          this.$confirm(str, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            resolve()
          }).catch(() => {
            reject()          
          });
        })
      },
      async clickSetCurrent(index){
        await this._confirm('生效该备份内容，并将接口模式设置为本地，确定？');
        let resObj = await setUseOneBackups(this.currentApiPath,index)
        this.apiContStr_current = await format(JSON.stringify(resObj.data))
        this.isUseLocalData = resObj.isUseLocalData
      },
      async clickUpdateOneBackups(index,item){
        await this._confirm('保存本次修改？');
        try{
          let res = await format(item._temp_apiContStr)
          let jsonData = res.replace(/[\n\s]/g,'')
          var newData = JSON.parse(jsonData)
        }catch(e){
          this.$message.error('更新备份失败：必须要输入标准Json格式')
          return
        }
        let resObj = await sendUpdateOneBackups(this.currentApiPath,index,newData,item.backupName)
        this.$message.success('更新成功')
      },
      async clickRemoveBackups(index){
        await this._confirm('一旦删除不可撤销，是否继续?');
        let resObj = await sendRemoveBackups(this.currentApiPath,index)
        this.handBackupsList(resObj.backups_datas)
      },
      // 新增备份
      async addNewBackups(){
        try{
          let res = await format(this.apiContStr_current)
          let jsonData = res.replace(/[\n\s]/g,'')
          var newData = JSON.parse(jsonData)
        }catch(e){
          this.$message.error('备份失败：数据格式有问题')
          return
        }
        let resObj = await sendAddNewBackups(this.currentApiPath,newData)
        this.handBackupsList(resObj.backups_datas)

      },
      // 加工backupsList formJson格式化
      async handBackupsList(arr){
        let newArr = []
        for(let d of arr){
          try{
            var jonsData =  await format(JSON.stringify(d.backupData||{}))
          }catch(e){
            var jonsData = {ret:0,data:'数据在备份过程中错乱'}
          }
          d._temp_apiContStr = jonsData
          newArr.push(d)
        }
        this.backupsList = newArr
      },
      async clickSearch(){
        if(!this.currentApiPath){
          this.$message.error('查询参数 url 不能为空')
          return
        }
        await this.getApiData();
        this.$message.success('接口数据获取成功')
      },
      async getApiData(){
        if(!this.currentApiPath){
          return
        }
        let resObj = await getApiInfoByPath(this.currentApiPath);
        this.apiInfo = resObj
        this.apiContStr_current = await format(JSON.stringify(resObj.data))
        this.handBackupsList(resObj.backups_datas)
        this.isUseLocalData = resObj.isUseLocalData
      },
      goBack(){
        this.$router.go(-1)
      }
    }
  }
</script>
<style rel="stylesheet/css" lang="css" scoped>
  .el-collapse-item__header{
    color:red !important;
  }
</style>


