import fetch from './fetch'
import { Message } from 'element-ui'

//========== 域名操作部分 hostList表 ==========
// 获取域名列表
export const getHostList = ()=> { 
    return fetch({
        url: '/_house_api/getHostList',
        method: 'post'
    })
}
// 获取当前正在生效的唯一域名
export const getCurrentHost = ()=> {
    return fetch({
        url: '/_house_api/getCurrentHost',
        method: 'post'
    })
}
// 修改当前域名
export const setCurrentHost = (host) => {
    return fetch({
        url: '/_house_api/set_currentHost',
        method: 'post',
        data: {
            host
        }
    })
}
// 增加新的域名
export const sendAddHost = (host) => {
    return fetch({
        url: '/_house_api/addHost',
        method: 'post',
        data: {
            host
        }
    })
}
// 增加新的域名
export const sendRemoveHost = (host) => {
    return fetch({
        url: '/_house_api/removeHost',
        method: 'post',
        data: {
            host
        }
    })
}

//========== apiData操作部分 site表 ==========
// 【查询接口列表】获取api列表
export const getApiData = (formData) => {
    return fetch({
        url: '/_house_api/getApiData',
        method: 'post',
        data: formData
    })
}
// 【模糊匹配接口url列表】根据url 模糊匹配接口列表 返回结果中只有url字段
export const getApiUrlList = path => {
    return fetch({
        url: '/_house_api/getApiDataByPath',
        method: 'post',
        data: {
            path,
            filterKeys:['path']//返回值筛选规则，只要url
        }
    })
}
// 【查询接口详情】根据url模糊匹配接口 返回第一个匹配上的接口详情
export const getApiInfoByPath = (path) => {
    return fetch({
        url: '/_house_api/getApiDataByPath',
        method: 'post',
        data: {
            path
        }
    }).then(resList=>{
        if (!resList.length) {
            Message({
                message: `无匹配结果`,
                type: 'warning'
            })
        }
        return resList[0]||{}
    })
}
// 【更新一个接口】更新某一个api接口数据
export const updateOne_apiData = (url, setData) => {
    return fetch({
        url: '/_house_api/updateOne_apiData',
        method: 'post',
        data: {
            url,
            ...setData
        }
    })
}
// 【更新一个接口】更新某一个api接口数据
export const updateOne_apiData_byId = (_id, setData) => {
    return fetch({
        url: '/_house_api/updateOne_apiData_byId',
        method: 'post',
        data: {
            _id,
            ...setData
        }
    })
}

// 【一键更新接口源】批量设置当前host所有接口的 数据来源 本地or线上
export const updateMany_apiDataByHost = setData => {
    return fetch({
        url: '/_house_api/updateMany_apiDataByHost',
        method: 'post',
        data: {
            ...setData
        }
    })
}
// 【删除接口】删除某一个接口 PS:会一并删除该接口的备份记录
export const removeOne_api = url => {
    return fetch({
        url: '/_house_api/removeOne_api',
        method: 'post',
        data: {
            url
        }
    })
}
// 【增加接口】
export const addOne_api = (path, newData, desc) => {
    return fetch({
        url: '/_house_api/addOne_api',
        method: 'post',
        data: {
            path, newData, desc
        }
    })
}


// 【增加备份】为某一个接口的历史备份列表中增加一个新的备份
export const sendAddNewBackups = (path,newData) => {
    return fetch({
        url: '/_house_api/insertOne_apiInfoBackups',
        method: 'post',
        data: {
            path,
            newData
        }
    })
}
// 【批量增加备份】一键备份，将当前所有接口动态缓存中的数据全部备份
export const sendAddAllNewBackups = () => {
    return fetch({
        url: '/_house_api/insertOneAll_apiInfoBackups',
        method: 'post',
    })
}

// 【删除备份】为某一个接口的历史备份列表中增加一个新的备份
export const sendRemoveBackups = (path, index) => {
    return fetch({
        url: '/_house_api/remove_apiInfoBackups',
        method: 'post',
        data: {
            path,
            index
        }
    })
}
// 【更新某个备份】为某一个接口的历史备份列表中增加一个新的备份
export const sendUpdateOneBackups = (path, index, newData, backupName) => {
    return fetch({
        url: '/_house_api/updateOne_apiInfoBackups',
        method: 'post',
        data: {
            path,
            index,
            newData,
            backupName
        }
    })
}
// 【启用备份】将该备份设置为使用状态
export const setUseOneBackups = (path, index) => {
    return fetch({
        url: '/_house_api/setUse_apiInfoBackups',
        method: 'post',
        data: {
            path,
            index
        }
    })
}
// 【批量启用备份，一键本地】将该备份设置为使用状态
export const setUseAllBackups = () => {
    return fetch({
        url: '/_house_api/setUseAll_apiInfoBackups',
        method: 'post'
    })
}





export const change_isUseLocalData = ({url,isUseLocalData}) => {
    return fetch({
        url: '/_house_api/change_isUseLocalData',
        method: 'post',
        data: {
            url,
            isUseLocalData
        }
    })
}






